import React, { useState } from "react";
import KakaoMap from "components/KakaoMap";
import { Link } from "react-router-dom";

export const MapContainer = ({ setData }) => {
  const [visible, setVisible] = useState(true);
  const [mapSize, setMapSize] = useState(["max-w-md", "400px"]);

  return (
    <div className="MapContainer">
      <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
        GPS 기능 추가중
      </label>
      <button type="button" onClick={() => setVisible((visible) => !visible)}>
        Toggle(Mount/Unmount)
      </button>
      <div id="wrap">
        {visible && (
          <>
            <KakaoMap size={mapSize} setData={setData} />
          </>
        )}
      </div>
    </div>
  );
};

export const MapListContainer = ({ userData, setModalToID }) => {
  const markerPositions = userData.map((test) => {
    return [test.location.lat, test.location.lng];
  });
  const markerUser = userData.map((user) => {
    return user;
  });
  const [mapSize, setMapSize] = useState(["100%", "100%"]);
  const MouseClick = (userData) => {
    console.log("마우스클릭처리");
    console.log(userData);
    setModalToID(() => userData);
  };
  const mouseOver = () => {
    console.log("마우스오버처리");
  };
  const mouseOut = () => {
    console.log("마우스아웃처리");
  };
  const mapClick = () => {
    console.log("맵클릭");
    setModalToID(() => null);
  };
  return (
    <>
      <KakaoMap
        CustomOverLayout={CustomOverLayout}
        markerPositions={markerPositions}
        markerUser={markerUser}
        size={mapSize}
        mouseOver={mouseOver}
        mouseOut={mouseOut}
        MouseClick={MouseClick}
        mapClick={mapClick}
      />
    </>
  );
};
const CustomOverLayout = () => {
  return <div>dad</div>;
};

export const Modal = ({ modalToID }) => {
  console.log("id", modalToID);
  return (
    <>
      {modalToID && (
        <Link to={`${modalToID.id}`}>
          <div className=" z-50 fixed  bottom-0 bg-white border-2 w-full h-44 p-2">
            <div className="flex justify-around">
              <div className="flex-row w-1/2 p-2 text-sm">
                <p>
                  <span className=" font-bold text-blue-700">물건: </span>
                  <span className=""> {modalToID.object}</span>
                </p>
                <p>
                  <span className=" font-bold text-blue-700">위치: </span>
                  <span className=""> {modalToID.place}</span>
                </p>
                <p>
                  <span className=" font-bold text-blue-700">메모: </span>
                  <span className=""> {modalToID.memo}</span>
                </p>
              </div>
              <div className=" w-1/2  h-36 p-1 bg-slate-200">
                <img
                  class="object-scale-down h-full w-full bg-slate-200"
                  src={modalToID.imagePath}
                  alt="이미지 없음"
                />
              </div>
            </div>
          </div>
        </Link>
      )}
    </>
  );
};
